@font-face {
  font-family: "Oswald";
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  margin: 0 auto;
}

.main-header {
  margin: 0 auto;
  width: 320px;
  position: relative;
}

.main-header__container {
  display: grid;
  grid-template-columns: 110px 1fr;
  align-items: center;
  row-gap: 13px;
  padding-top: 10px;
}

.main-header__logo {
  padding-left: 20px;
}
.main-header__logo:hover {
  opacity: 0.6;
}
.main-header__logo:active {
  opacity: 0.3;
}

.main-header__logo-name {
  padding-top: 5px;
}
.main-header__logo-name:hover {
  opacity: 0.6;
}
.main-header__logo-name:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .main-header {
    margin: 0 auto;
    width: 768px;
  }

  .main-header__logo {
    padding: 0;
    width: 174px;
    height: 52px;
  }

  .logo-icon__link {
    width: 174px;
    height: 52px;
  }

  .main-header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 28px 30px 30px;
  }

  .main-header__logo-name {
    margin-left: 12px;
    display: none;
  }
}
@media (min-width: 1440px) {
  .main-header {
    margin: 0 auto;
    width: 1440px;
  }

  .main-header__logo {
    width: 202px;
    height: 59px;
  }

  .logo-icon__link picture {
    display: block;
    width: 202px;
    height: 59px;
    margin: 0;
  }

  .main-header__container {
    padding: 54px 110px 29px;
  }
}
.site-nav__list {
  position: absolute;
  top: 65px;
  right: 0;
  z-index: 1;
  padding: 0;
  margin: 0;
  list-style-type: none;
  background-color: #ffffff;
  width: 320px;
}

.burger-button {
  position: absolute;
  top: 22px;
  right: 20px;
  width: 24px;
  height: 18px;
  z-index: 2;
  background-color: transparent;
  border: 0;
  cursor: pointer;
}

.site-nav--closed .burger-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

@media (max-width: 767px) {
  .site-nav--closed .site-nav__item {
    display: none;
  }
  .site-nav--closed .site-nav__item--active {
    display: block;
  }
}
.site-nav--opened .burger-button::before, .site-nav--opened .burger-button::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
}
.site-nav--opened .burger-button::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.site-nav--opened .burger-button::after {
  transform: rotate(-45deg);
}

.site-nav__item {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  padding: 22px 0 24px;
  border-top: 1px solid #E6E6E6;
  position: relative;
}
.site-nav__item a {
  color: #000000;
  text-decoration: none;
}
.site-nav__item:hover {
  opacity: 0.6;
}
.site-nav__item:active {
  opacity: 0.3;
}

.site-nav--nojs .site-nav__item {
  display: flex;
}

.site-nav--nojs .site-nav__list {
  position: static;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .site-nav__list {
    position: static;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 440px;
    background: transparent;
  }

  .site-nav__burger-button {
    display: none;
  }

  .site-nav__item {
    padding: 0;
    line-height: 24px;
    font-size: 18px;
    border: 0;
    font-weight: 400;
  }

  .site-nav__item--main-green::after {
    content: "";
    position: absolute;
    width: 71px;
    height: 2px;
    background-color: #68B738;
    bottom: -5px;
  }

  .site-nav__item--current::after {
    content: "";
    position: absolute;
    width: 168px;
    height: 2px;
    background-color: #68B738;
    bottom: -5px;
  }
}
@media (min-width: 1440px) {
  .site-nav__list {
    position: static;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 478px;
  }

  .site-nav__burger-button {
    display: none;
  }

  .site-nav__item {
    font-size: 20px;
    line-height: 30px;
    border: 0;
    z-index: 2;
  }
  .site-nav__item a {
    font-size: 20px;
    line-height: 30px;
  }

  .site-nav__item--secondary a {
    color: #ffffff;
  }

  .site-nav__item--main::after {
    content: "";
    position: absolute;
    width: 71px;
    height: 2px;
    background-color: #ffffff;
    bottom: -5px;
  }
}
.site-preview {
